import React from 'react';
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr'
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';
import MarketTypeSearch from '../../SearchComponents/MarketTypeSearch/MarketTypeSearch';
import MarketNamesTables from "./Tables/MarketNamesTables";
import MarketNamesModals from "./Modals/MarketNamesModals"
import SubCountyTypeSearch from '../../SearchComponents/SubCountyTypeSearch/SubCountyTypeSearch';
import WardsSearch from '../../SearchComponents/WardsSearch/WardsSearch';

class MarketNames extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            validated: false,
            columns: [],
            data: [],
            filteredData: [],
            filteredMarketTypeData: [],
            filteredStatusData: [],
            value: '',
            show: false,
            showdelete: false,
            SET_MARKET_NAME_ID: 0,
            MARKET_CODE: 0,
            MARKET_NAME: '',
            SET_MARKET_TYPE_ID: 0,
            NUM_STALLS: 0,
            SYS_STATUS_ID: 0,
            SET_SUBCOUNTY_ID: 0,
            SET_WARD_ID: 0,
            CREATEDBY: props.user.EMAIL,
            MODIFIEDBY: props.user.EMAIL,
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: '',

            openmarkettypesearch: false,
            openstatussearch: false,

            MARKETTYPEDESCRIPTION: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',

            searchcolumns: [],

            searchdata: [],
            searchmarkettypedata: [],
            searchstatusdata: [],

            searchFilteredMarketTypeData: [],
            searchFilteredStatusData: [],

            searchmarkettypevalue: '',
            searchstatusvalue: '',

            opensubcountysearch: false,
            searchFilteredSubCountyData: [],
            searchsubcountyvalue: '',

            openwardsearch: false,
            searchFilteredWardData: [],
            searchwardvalue: '',

        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowMarketTypeSearch = () => {
            this.setState({ openmarkettypesearch: true });
        };

        this.handleShowStatusSearch = () => {
            this.setState({ openstatussearch: true });
        };

        this.handleShowSubCountySearch = () => {
            this.setState({ opensubcountysearch: true });
        };

        this.handleShowWardSearch = () => {
            this.setState({ openwardsearch: true });
        };

        this.handleHideMarketTypeSearch = () => {
            this.setState({ openmarkettypesearch: false });
        };

        this.handleHideStatusSearch = () => {
            this.setState({ openstatussearch: false });
        };

        this.handleHideSubCountySearch = () => {
            this.setState({ opensubcountysearch: false });
        };

        this.handleHideWardSearch = () => {
            this.setState({ openwardsearch: false });
        };

        this.populateData();
        this.populateTypes();
        this.populateStatus();
        this.populateSubCounty();

    }


    populateData() {
        const getdata = `
            query{
                AllMarketNames{
                    SET_MARKET_NAME_ID
                    MARKET_CODE
                    MARKET_NAME
                    SET_MARKET_TYPE_ID   
                    MARKET_TYPE{
                        MARKET_TYPE_ID
                        MARKET_TYPE
                    }
                    NUM_STALLS
                    TOTAL_ALLOCATED
                    TOTAL_STALL_REMAINING
                    SYS_STATUS_ID
                    SYS_STATUS_TYPE{
                        SYS_STATUS_ID
                        SYS_STATUS_NAME
                    }
                    SET_SUBCOUNTY_ID
                    SUBCOUNTY{
                        SUBCOUNTY_ID
                        SUBCOUNTY_NAME
                    }
                    SET_WARD_ID
                    WARD{
                      WARD_ID
                      WARD_NAME
                    }

                    CREATEDBY
                    MODIFIEDBY
                    CREATED_DATE
                    MODIFIED_DATE
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.AllMarketNames
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    populateTypes() {

        const getdata = `
            query {
                AllMarketTypes {
                    MARKET_TYPE_ID,
                    MARKET_TYPE
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchmarkettypedata: data.data.AllMarketTypes
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to SubCounties Load Market Type Data');
                }
            })
    }

    populateStatus() {
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"SET"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchstatusdata: data.data.SystemStatusFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load System Status Type Data');
                }
            })
    }

    populateSubCounty() {
        const getdata = `
            query {
                SubCounties {
                    SUBCOUNTY_ID,
                    SUBCOUNTY_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchsubcountydata: data.data.SubCounties
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Sub County Data');
                }
            })
    }


    populateWard(value) {
        const getdata = `
        query {
            WardFilter 
            (
                SET_SUBCOUNTY_ID:${value}
            ) 
           {
                WARD_ID
                WARD_NAME
           }
       }
            
    `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchwarddata: data.data.WardFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Wards Data');
                }
            })
    }

    handleSubmit = (event) => {
        event.preventDefault();

        const validate = event.currentTarget;
        if (validate.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        else {

            const form = `
            mutation{
                addMarketName(
                    MARKET_CODE:${this.state.MARKET_CODE},
                    MARKET_NAME:"${this.state.MARKET_NAME}",
                    SET_MARKET_TYPE_ID:${this.state.SET_MARKET_TYPE_ID},
                    NUM_STALLS:${this.state.NUM_STALLS},
                    SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    SET_SUBCOUNTY_ID:${this.state.SET_SUBCOUNTY_ID},
                    SET_WARD_ID:${this.state.SET_WARD_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.MODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        SET_MARKET_NAME_ID
                }
            }
            `;

            const formEdit = `
            mutation{
                updateMarketName(
                    SET_MARKET_NAME_ID:${this.state.SET_MARKET_NAME_ID},
                    MARKET_CODE:${this.state.MARKET_CODE},
                    MARKET_NAME:"${this.state.MARKET_NAME}",
                    SET_MARKET_TYPE_ID:${this.state.SET_MARKET_TYPE_ID},
                    NUM_STALLS:${this.state.NUM_STALLS},
                    SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    SET_SUBCOUNTY_ID:${this.state.SET_SUBCOUNTY_ID},
                    SET_WARD_ID:${this.state.SET_WARD_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.MODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        SET_MARKET_NAME_ID
                }
            }
            `;
            console.log(formEdit);
            if (this.state.InsertUpdate === 'Insert') {
                API.create(form)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }

        }
        this.setState({
            validated: true
        })
    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deleteMarketName(
                SET_MARKET_NAME_ID:${this.state.SET_MARKET_NAME_ID}
                ){
                    SET_MARKET_NAME_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseMarketTypeSearch = (e) => {

        this.setState({
            SET_MARKET_TYPE_ID: e.MARKET_TYPE_ID,
            MARKETTYPEDESCRIPTION: e.MARKET_TYPE,
            openmarkettypesearch: false,
        });
    };

    handleCloseStatusSearch = (e) => {

        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            openstatussearch: false,
        });
    };

    handleCloseSubCountySearch = (e) => {

        this.setState({
            SET_SUBCOUNTY_ID: e.SUBCOUNTY_ID,
            SUBCOUNTY_NAME: e.SUBCOUNTY_NAME,
            WARD_NAME: '',
            opensubcountysearch: false,
        });
        this.populateWard(e.SUBCOUNTY_ID);
    };

    handleCloseWardSearch = (e) => {

        this.setState({
            SET_WARD_ID: e.WARD_ID,
            WARD_NAME: e.WARD_NAME,
            openwardsearch: false,
        });
    };

    handleEditData = (e) => {
        this.setState({
            SET_MARKET_NAME_ID: e.SET_MARKET_NAME_ID,
            MARKET_CODE: e.MARKET_CODE,
            MARKET_NAME: e.MARKET_NAME,

            SET_MARKET_TYPE_ID: e.SET_MARKET_TYPE_ID,
            MARKETTYPEDESCRIPTION: e.MARKET_TYPE.MARKET_TYPE,

            NUM_STALLS: e.NUM_STALLS,

            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_TYPE.SYS_STATUS_NAME,

            SET_SUBCOUNTY_ID: e.SET_SUBCOUNTY_ID,
            SUBCOUNTY_NAME: e.SUBCOUNTY.SUBCOUNTY_NAME,

            SET_WARD_ID: e.SET_WARD_ID,
            WARD_NAME: e.WARD.WARD_NAME,

            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            SET_MARKET_NAME_ID: e.SET_MARKET_NAME_ID,
            MARKET_CODE: e.MARKET_CODE,
            MARKET_NAME: e.MARKET_NAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            MARKET_CODE: '',
            MARKET_NAME: '',
            NUM_STALLS: '',
            MARKETTYPEDESCRIPTION: '',
            SYS_STATUS_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',
            SUBCOUNTY_NAME: '',
            WARD_NAME: '',
            validated: false
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_TYPE.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.MARKET_CODE.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.MARKET_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.NUM_STALLS.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.MARKET_TYPE.MARKET_TYPE.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SUBCOUNTY.SUBCOUNTY_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.WARD.WARD_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_TYPE.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.MARKET_CODE.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.MARKET_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.NUM_STALLS.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.MARKET_TYPE.MARKET_TYPE.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.SUBCOUNTY.SUBCOUNTY_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.WARD.WARD_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchMarketTypeFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchmarkettypedata
        let filteredMarketTypeData = this.state.searchFilteredMarketTypeData
        this.setState({ searchmarkettypevalue: value });

        if (value.length) {
            filteredMarketTypeData = data.filter(item => {
                let startsWithCondition =
                    item.MARKET_TYPE.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.MARKET_TYPE.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredMarketTypeData })
        }
    }

    handleSearchStatusFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchstatusdata
        let filteredStatusData = this.state.searchFilteredStatusData
        this.setState({ searchstatusvalue: value });

        if (value.length) {
            filteredStatusData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredStatusData })
        }
    }

    handleSearchSubCountyFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchsubcountydata
        let filteredSubCountyData = this.state.searchFilteredSubCountyData
        this.setState({ searchsubcountyvalue: value });

        if (value.length) {
            filteredSubCountyData = data.filter(item => {
                let startsWithCondition =
                    item.SUBCOUNTY_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SUBCOUNTY_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredSubCountyData })
        }
    }

    handleSearchWardFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchwarddata
        let filteredWardData = this.state.searchFilteredWardData
        this.setState({ searchwardvalue: value });

        if (value.length) {
            filteredWardData = data.filter(item => {
                let startsWithCondition =
                    item.WARD_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.WARD_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredWardData })
        }
    }

    calculateTotalStalls = (e) => {

        this.setState({
            TOTAL_STALLS_ALLOCATED: e.target.value
        });
        this.calculateRemainingStalls();
    }

    calculateRemainingStalls = () => {
        let Remaining = 0;
        Remaining = parseInt(this.state.NUM_STALLS) - parseInt(this.state.TOTAL_STALLS_ALLOCATED)
        this.setState({
            TOTAL_STALLS_REMAINING: Remaining
        });
        return Remaining;
    }

    render() {

        const { data, columns, value, filteredData,
            filteredMarketTypeData, searchmarkettypevalue, searchmarkettypedata,
            filteredStatusData, searchstatusvalue, searchstatusdata,
            searchsubcountyvalue, searchsubcountydata, filteredSubCountyData,
            searchwardvalue, searchwarddata, filteredWardData
        } = this.state

        return (
            <>
                <Helmet><title>Market Names</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Market Names"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Market Names</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <MarketTypeSearch
                                        handleSearchMarketType={this.handleCloseMarketTypeSearch}
                                        opensearch={this.state.openmarkettypesearch}
                                        handleHideSearch={this.handleHideMarketTypeSearch}
                                        value={searchmarkettypevalue}
                                        data={searchmarkettypedata}
                                        handleFilter={this.handleSearchMarketTypeFilter}
                                        filteredData={filteredMarketTypeData}
                                    />

                                    <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseStatusSearch}
                                        opensearch={this.state.openstatussearch}
                                        handleHideSearch={this.handleHideStatusSearch}
                                        value={searchstatusvalue}
                                        data={searchstatusdata}
                                        handleFilter={this.handleSearchStatusFilter}
                                        filteredData={filteredStatusData}
                                    />

                                    <SubCountyTypeSearch
                                        handleSearchSubCounty={this.handleCloseSubCountySearch}
                                        opensearch={this.state.opensubcountysearch}
                                        handleHideSearch={this.handleHideSubCountySearch}
                                        value={searchsubcountyvalue}
                                        data={searchsubcountydata}
                                        handleFilter={this.handleSearchSubCountyFilter}
                                        filteredData={filteredSubCountyData}
                                    />

                                    <WardsSearch
                                        handleSearchWards={this.handleCloseWardSearch}
                                        opensearch={this.state.openwardsearch}
                                        handleHideSearch={this.handleHideWardSearch}
                                        value={searchwardvalue}
                                        data={searchwarddata}
                                        handleFilter={this.handleSearchWardFilter}
                                        filteredData={filteredWardData}
                                    />

                                    <MarketNamesTables
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        columns={columns}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <MarketNamesModals
                                        validated={this.state.validated}
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowMarketTypeSearch={this.handleShowMarketTypeSearch}
                                        handleShowStatusSearch={this.handleShowStatusSearch}
                                        handleShowSubCounty={this.handleShowSubCountySearch}
                                        handleShowWard={this.handleShowWardSearch}

                                        MarketTypeDescription={this.state.MARKETTYPEDESCRIPTION}
                                        handleChangeMarketTypeDescription={e => this.setState({ MARKETTYPEDESCRIPTION: e.target.value })}

                                        MarketCode={this.state.MARKET_CODE}
                                        changeMarketCode={e => this.setState({ MARKET_CODE: e.target.value })}

                                        MarketName={this.state.MARKET_NAME}
                                        changeMarketName={e => this.setState({ MARKET_NAME: e.target.value })}

                                        NumStalls={this.state.NUM_STALLS}
                                        changeNumStalls={e => this.setState({ NUM_STALLS: e.target.value })}

                                        SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}
                                        handleChangeSystemStatusTypeDescription={e => this.setState({ SYSTEMSTATUSTYPEDESCRIPTION: e.target.value })}

                                        SubCountyName={this.state.SUBCOUNTY_NAME}
                                        handleChangeSubCountyName={e => this.setState({ SUBCOUNTY_NAME: e.target.value })}

                                        WardName={this.state.WARD_NAME}
                                        handleChangeWardName={e => this.setState({ WARD_NAME: e.target.value })}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(MarketNames));
