import React from 'react';
import { Button, Modal, Form, InputGroup, Col } from 'react-bootstrap';
import { Search } from 'react-feather';

class MarketNamesModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Market Names
                        </Modal.Title>
                    </Modal.Header>

                    <Form noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="MarketCode">
                                    <Form.Label>Market Code</Form.Label>
                                    <Form.Control type="number"
                                        name="MarketCode"
                                        placeholder="Enter Market Code"
                                        value={this.props.MarketCode}
                                        onChange={this.props.changeMarketCode}
                                        required
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter Market Code.
                                    </Form.Control.Feedback>
                                </Form.Group>

                                <Form.Group as={Col} md="6" controlId="MarketName">
                                    <Form.Label>Market Name</Form.Label>
                                    <Form.Control type="text"
                                        name="MarketName"
                                        placeholder="Enter Market Name"
                                        value={this.props.MarketName}
                                        onChange={this.props.changeMarketName}
                                        required
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter Market Name.
                                    </Form.Control.Feedback>
                                </Form.Group>
                            </Form.Row>

                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="NumStalls">
                                    <Form.Label>No Of Stalls</Form.Label>
                                    <Form.Control type="number"
                                        name="NumStalls"
                                        placeholder="Enter No Of Stalls"
                                        value={this.props.NumStalls}
                                        onChange={this.props.changeNumStalls}
                                        required
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter No Of Stalls.
                                    </Form.Control.Feedback>
                                </Form.Group>
                            </Form.Row>

                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="SubCounty">
                                    <Form.Label>SubCounty Name</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            name="SubCounty"
                                            value={this.props.SubCountyName}
                                            placeholder="Search SubCounty Name"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowSubCounty}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Sub County.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>

                                <Form.Group as={Col} md="6" controlId="Ward">
                                    <Form.Label>Ward Name</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            name="Ward"
                                            value={this.props.WardName}
                                            placeholder="Search Ward Name"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowWard}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Ward.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                        </Form.Row>

                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="MarketType">
                                    <Form.Label>Market Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            name="MarketType"
                                            value={this.props.MarketTypeDescription}
                                            placeholder="Search Market Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowMarketTypeSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Market Type.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>

                                <Form.Group as={Col} md="6" controlId="SystemStatusType">
                                    <Form.Label>System Status Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            name="SystemStatus"
                                            value={this.props.SystemStatusTypeDescription}
                                            placeholder="Search System Status Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowStatusSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select status.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>
                            
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Market Code
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description_del">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.MarketCode} onChange={this.props.changeMarketCode} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default MarketNamesModal;